  justify-content: space-between;
  width: 100%;
  align-items: center;
  grid-area: nav; }
  .navbar .logo-img img {
    width: 100%;
    padding: 3.5%; }
  .navbar .logo-back {
    display: none; }
  .navbar .logo-front:hover .logo-back {
    display: block; }
  .navbar ul {
    display: flex;
    justify-content: center;
    align-items: center; }
    .navbar ul li {
      padding-right: 8rem; }
      .navbar ul li a {
        color: black;
        font-size: 2em; }
        .navbar ul li a:hover {
          border-bottom: 2px solid black; }
  .navbar .search {
    margin-right: 4rem; }
    .navbar .search input {
      width: 12rem;
      height: 3vh;
      background-color: yellow;
      border: 2px solid black; }
    .navbar .search button {
      height: 3vh;
      color: white;
      padding: 0.3rem;
      background-color: black;
      border: none; }

.cover-container {
  width: 100%;
  grid-area: cover; }
  .cover-container figure {
    position: relative; }
    .cover-container figure img {
      min-width: 100%;
      height: 100vh; }
    .cover-container figure figcaption {
      position: absolute;
      top: 45%;
      right: 0%;
      font-size: 4rem;
      color: white;
      padding: 0.5rem;
      padding-left: 2rem;
      padding-right: 25rem;
      background-color: black; }
    .cover-container figure span {
      color: yellow; }

figure {
  width: 100%;
  position: relative; }

figcaption {
  position: absolute;
  top: 27%;
  font-size: 4rem;
  color: white;
  padding: 0.5rem;
  padding-left: 25rem;
  padding-right: 2rem;
  background-color: black; }

.event-container {
