  grid-template-areas: "eventcover eventcover aside"; }
  .event-container .eventCover-container {
    grid-area: eventcover; }
    .event-container .eventCover-container img {
      width: 100%;
      margin-top: -.3rem; }
  .event-container .aside-event {
    margin-top: -0.3rem;
    grid-area: aside;
    background-color: yellow;
    height: 96.6vh; }
    .event-container .aside-event .event {
      text-align: left;
      margin: 12rem auto;
      margin-left: 5rem; }
      .event-container .aside-event .event h2 {
