      .event-container .aside-event .event span {
        font-size: 1.3em;
        font-weight: 700; }
      .event-container .aside-event .event li {
        font-size: 1.3em; }
      .event-container .aside-event .event li:nth-of-type(1) {
        padding-top: 1.5rem; }
      .event-container .aside-event .event li:nth-of-type(2) {
        padding-top: 0; }
      .event-container .aside-event .event li:nth-of-type(4) {
        padding-top: 2rem; }
      .event-container .aside-event .event li:nth-of-type(6) {
        padding-top: 2rem; }

.shop-container {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-areas: "aside shop shop";
  margin-bottom: -5rem; }
  .shop-container .shop-list-container {
    grid-area: aside;
    margin-top: -1rem;
    height: 80vh;
    background-color: yellow; }
  .shop-container .shopItems-container {
    grid-area: shop;
    position: relative; }
    .shop-container .shopItems-container h2 {
      position: absolute;
      top: 20%;
      right: 0%;
      font-size: 4rem;
      color: white;
      padding: 0.5rem;
      padding-left: 2rem;
      padding-right: 15rem;
      background-color: black; }

.shop-list {
  padding: 10%; }
  .shop-list h2 {
    font-size: 3em;
    margin-top: 6rem;
    padding-bottom: 1rem;
    font-weight: 700; }
  .shop-list li {
    font-size: 1.5em; }
  .shop-list li:nth-of-type(1) {
    font-size: 2em;
    font-weight: 700;
    padding-bottom: 0.4rem; }
  .shop-list li:nth-of-type(3) {
    padding-top: 1rem; }
  .shop-list li:nth-of-type(4) {
    padding-top: 1rem; }
  .shop-list li:nth-of-type(5) {
    padding-top: 1rem; }
  .shop-list li:nth-of-type(6) {
    padding-top: 1rem; }
  .shop-list li:nth-of-type(7) {
    padding-top: 1rem; }

.shoes {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: "shoe1 shoe2 shoe3" "shoe4 shoe5 shoe6";
  grid-gap: 2rem;
  padding: 10%;
  margin-top: 9%;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  -webkit-transition: visibility .25s ease-in, -webkit-transform .25s;
  transition: visibility .25s ease-in, -webkit-transform .25s;
  transition: transform .25s, visibility .25s ease-in;
  transition: transform .25s, visibility .25s ease-in, -webkit-transform .25s; }
  .shoes img {
    width: 60%;
    display: block;
    margin: 0 auto;
    -webkit-transition: all .2s;
    transition: all .2s;
    overflow: hidden; }
    .shoes img:hover {
      -webkit-transform: scale(0.9);
              transform: scale(0.9); }
  .shoes p {
    text-align: center; }
  .shoes li {
    border: 2px solid black;
    height: 20vh; }
  .shoes li:nth-of-type(1) {
    grid-area: shoe1; }
  .shoes li:nth-of-type(2) {
    grid-area: shoe2; }
  .shoes li:nth-of-type(3) {
    grid-area: shoe3; }
  .shoes li:nth-of-type(4) {
    grid-area: shoe4; }
  .shoes li:nth-of-type(5) {
    grid-area: shoe5; }
  .shoes li:nth-of-type(6) {
    grid-area: shoe6; }

.contact-container {
  display: grid;
  grid-template-columns: 1fr 1fr 2fr;
  grid-template-areas: "aside aside form";
  margin-top: 5rem; }
  .contact-container img {
    width: 100%; }

.contact-aside {
  grid-area: aside;
  position: relative;
  margin-top: -5.4rem; }
  .contact-aside h2 {
    position: absolute;
    top: 27%;
    font-size: 4rem;
    color: black;
    padding: 0.5rem;
    padding-left: 25rem;
    padding-right: 2rem;
    background-color: yellow; }
  .contact-aside .contact-footer {
    text-align: center;
    background-color: black;
    color: white;
    margin-top: -0.3rem;
    padding: 5%; }
    .contact-aside .contact-footer h3 {
      font-size: 2em; }
    .contact-aside .contact-footer li {
      padding-top: 0.3rem;
      color: white;
      line-height: 1.3; }
    .contact-aside .contact-footer a {
      text-decoration: underline;
      color: white; }

.contact-form {
  grid-area: form; }
  .contact-form h3 {
    font-size: 2.4em;
    margin-left: 20%;
    margin-top: 8%; }
  .contact-form form {
    margin-left: 20%;
    margin-right: 25%;
    margin-top: 4%;
    width: 50%;
    font-size: 1.4em;
    font-weight: 700; }
    .contact-form form label {
      margin-top: 1.5rem;
      display: block; }
    .contact-form form input {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      height: 3.5vh;
      width: 60%;
      border: 2px solid black;
      background-color: yellow; }
    .contact-form form .text-box {
      height: 15vh; }
    .contact-form form button {
      border: none;
      background-color: black;
      color: white;
      width: 25%;
      padding: 0.5rem;
      margin-top: 1rem; }

.App {
  display: grid;
  width: 100%;
  grid-auto-columns: 1fr;
  grid-template-areas: "nav" "wrapper"; }
  .App .wrapper {
    display: grid;
    grid-area: wrapper;
    grid-template-columns: 1fr;
    grid-template-areas: "cover" "event" "shop" "contact"; }
    .App .wrapper .cover-container {
      grid-area: cover; }
    .App .wrapper .event-container {
      grid-area: event; }
    .App .wrapper shop-container {
      grid-area: shop; }
    .App .wrapper .contact-container {
      grid-area: contact; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  -webkit-user-drag: none;
  line-height: 1.2;
  font-family: 'Raleway', sans-serif; }

.active {
  background-color: yellow;
  padding: 1.2rem; }

